import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsComplexLocationExposed } from './ps-complex-location.component.interface';


@Component({
  selector: 'ps-complex-location',
  templateUrl: './ps-complex-location.component.html',
})
export class PsComplexLocationComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsComplexLocationExposed;
  @Output("parentFun") parentFun: EventEmitter<any> = new EventEmitter();
  branch = CommonBussinessConstant.SPECIFIC_BRANCH_VALUE;
  home = CommonBussinessConstant.HOME_ADDRESS_VALUE;
  work = CommonBussinessConstant.WORK_ADDRESS_VALUE;
  other = CommonBussinessConstant.OTHER_ADDRESS_VALUE;
  enableCifBranch: any;
  requestVO: any;
  cifInfo: any;
  collectionType: string = null;
  constructor(private omniPull: OmniPullService, public commonProv: PsCommonService, public loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.requestVO = this.options.homeAddressOptions.requestVO;
    if (this.options.cifBranchChecking == true) {
      this.fetchParameter();
    }
  }
  onCollectionModeChanged(data) {
    if (data && data.selectedObj) {
      this.collectionType = data.selectedObj.itemValue;
      this.resetOldData(data.selectedObj.itemValue)
      this.parentFun.emit();
    }
  }
  resetOldData(type) {
    if (type == this.home) {
      this.commonProv.setValInsideNestedObj(this.options.workAddressOptions.fcName, '', this.requestVO);
      this.commonProv.setValInsideNestedObj(this.options.otherAddressOptions.fcName, '', this.requestVO);
    } else if (type == this.work) {
      this.commonProv.setValInsideNestedObj(this.options.homeAddressOptions.fcName, '', this.requestVO);
      this.commonProv.setValInsideNestedObj(this.options.otherAddressOptions.fcName, '', this.requestVO);
    } else if (type == this.other) {
      this.commonProv.setValInsideNestedObj(this.options.homeAddressOptions.fcName, '', this.requestVO);
      this.commonProv.setValInsideNestedObj(this.options.workAddressOptions.fcName, '', this.requestVO);
    }
  }

  async fetchParameter() {
    this.cifInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);

    const result = await this.omniPull.getParamValOf(CommonBussinessConstant.COLLECTION_DESTINATION_BRANCH)
      .catch((error) => {
        this.logger.log(error);
      });
    if (result !== -1 && result.CollectionDestinationBranch === CommonBussinessConstant.END_USER_INPUT_ID) {
      this.enableCifBranch = 0;
    } else {
      if (result.CollectionDestinationBranch === CommonBussinessConstant.ACCOUNT_BRANCH_ID) {
        this.options.branchCifOptions.labelKey = "delivery_account_branch_key";
        this.commonProv.setValInsideNestedObj(this.options.branchCifOptions.fcName, this.cifInfo.cifBranch ? this.cifInfo.customerInfoCO.branchCode : '', this.requestVO);
      } else {
        this.options.branchCifOptions.labelKey = "delivery_cif_branch_key";
        this.commonProv.setValInsideNestedObj(this.options.branchCifOptions.fcName, this.cifInfo.cifBranch ? this.cifInfo.customerInfoCO.branchBriefDesc : '', this.requestVO);
      }
      this.enableCifBranch = 1;
    }
    this.hideShowDeliveryBranch();
    this.parentFun.emit();
  }

  hideShowDeliveryBranch() {
    let enablelocation = this.enableCifBranch == 1 ? 0 : 1;
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE,
      [this.options?.branchCifOptions.fcName], this.enableCifBranch);

    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.options.otherAddressOptions.fcName,
    this.options.workAddressOptions.fcName, this.options.homeAddressOptions.fcName,
    this.options.collectionModeOptions.fcName, this.options.branchLocationOptions.fcName], enablelocation);

  }
}
